class Api {
  constructor(config) {
    this._url = config.baseUrl;
    this._headers = config.headers;
    this._cardsUrl = config.cardsUrl;
    this._likesUrl = config.likesUrl;
    this._usersUrl = config.usersUrl;
    this._userUrl = config.userUrl;
    this._avatarUrl = config.avatarUrl;
    this._check = (res) => {
      if (res.ok) { return res.json() }
      return Promise.reject(`Error: ${res.status}`)
    }
  }

  // –ü–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –∫–∞—Ä—Ç–æ—á–µ–∫ —Å —Å–µ—Ä–≤–µ—Ä–∞ //
  getInitialCards() {
    return fetch(`${this._url}${this._cardsUrl}`, {
      method: 'GET',
      headers: this._headers,
    })
      .then(this._check);
  };

  // –î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –Ω–∞ —Å–µ—Ä–≤–µ—Ä //
  addCard(data) {
    return fetch(`${this._url}${this._cardsUrl}`, {
      method: 'POST',
      headers: this._headers,
      body: JSON.stringify({
        name: data.name,
        link: data.link
      })
    })
      .then(this._check);
  };

  // –ü–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è //
  getUserInfo() {
    return fetch(`${this._url}${this._usersUrl}${this._userUrl}`, {
      method: 'GET',
      headers: this._headers,
    })
      .then(this._check);
  }

  // –ò–∑–º–µ–Ω–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è //
  editUserInfo(data) {
    return fetch(`${this._url}${this._usersUrl}${this._userUrl}`, {
      method: 'PATCH',
      headers: this._headers,
      body: JSON.stringify({
        name: data.name,
        about: data.about
      })
    })
      .then(this._check);
  }

  // –ò–∑–º–µ–Ω–∏—Ç—å –∞–≤–∞—Ç–∞—Ä //
  editUserPic(image) {
    return fetch(`${this._url}/users/me/avatar`, {
      method: 'PATCH',
      headers: this._headers,
      body: JSON.stringify(image)
    })
      .then(this._check);
  }

  // –£–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É //
  deleteCard(cardId) {
    return fetch(`${this._url}${this._cardsUrl}/${cardId}`, {
      method: 'DELETE',
      headers: this._headers,
    })
      .then(this._check);
  }

  // –ü–æ—Å—Ç–∞–≤–∏—Ç—å/—Å–Ω—è—Ç—å –ª–∞–π–∫ //
    changeLikeCardStatus(cardId, isLiked) {
    return fetch(`${this._url}${this._cardsUrl}/likes/${cardId}`, {
      method: isLiked ? 'PUT' : 'DELETE',
      headers: this._headers
    })
      .then(res => {
        if (res.ok) {
          return res.json();
        }

        return Promise.reject(`–û—à–∏–±–∫–∞: ${res.status}`);
      });
  }

}

// Api - –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏—è //
const api = new Api({
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-18',
  cardsUrl: '/cards',
  usersUrl: '/users',
  userUrl: '/me',
  avatarUrl: '/me/avatar',
  headers: {
    authorization: 'eaa08385-02d1-499c-a13e-a2b5f60e8932',
    'Content-Type': 'application/json'
  }
});

export default api;