import React, { useEffect, useContext } from 'react';
import PopupWithForm from './PopupWithForm';
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function EditProfilePopup(props) {

  const currentUser = useContext(CurrentUserContext);

  // –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è –∏–º–µ–Ω–∏/–æ–ø–∏—Å–∞–Ω–∏—è //
  const [name, setName] = React.useState(currentUser.name);
  function handleNameChange(evt) {
    setName(evt.target.value);
  }

  const [description, setDescription] = React.useState(currentUser.about);
  function handleDescriptionChange(evt) {
    setDescription(evt.target.value);
  }

  // –•—É–∫ –Ω–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∫–æ–Ω—Ç–µ–∫—Å—Ç–∞ //
  useEffect(() => {
    if (!props.isOpen) {
      setName(currentUser.name);
      setDescription(currentUser.about);
    }
  }, [currentUser, props]);

  // –°–∞–±–º–∏—Ç —Ñ–æ—Ä–º—ã –ø—Ä–æ—Ñ–∏–ª—è //
  function handleSubmit(evt) {
    evt.preventDefault();
    props.onUpdateUser({
      name,
      about: description,
    });
  } 

  return (
    <PopupWithForm
      name="user"
      title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
      {...props}
      onSubmit={handleSubmit}
    >
      <input
        type="text"
        required
        autoComplete="off"
        minLength="2"
        maxLength="40"
        placeholder="–ò–º—è"
        className="popup__input popup__input_name popup__input_name-profile"
        id="name"
        name="name"
        value={name || ''}
        onChange={handleNameChange}
      />
      <span
        id="name-error"
         className="error">
      </span>
      <input
        type="text"
        required autoComplete="off"
        minLength="2" maxLength="200"
        placeholder="–†–æ–¥ –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏"
        className="popup__input popup__input_description popup__input_description-profile"
        id="job"
        name="info"
        value={description || ''}
        onChange={handleDescriptionChange}
      />
      <span
        id="job-error"
        className="error">
      </span>
      <button
        type="submit"
        className="popup__save">
        –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
      </button>
    </PopupWithForm>
  );

}

export default EditProfilePopup;