import React, { useEffect, useState } from 'react';
import '../index.css';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import EditProfilePopup from './EditProfilePopup';
import AddPlacePopup from './AddPlacePopup';
import EditAvatarPopup from './EditAvatarPopup';
import PopupWithForm from './PopupWithForm';
import ImagePopup from './ImagePopup';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import api from '../utils/api';

function App() {

  // –•—É–∫–∏ –¥–ª—è –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ //
  const [currentUser, set–°urrentUser] = useState('');
  useEffect(() => {
    api.getUserInfo()
      .then(res => {
        set–°urrentUser(res)
      })
      .catch((error) => console.error(error))
  }, []);

  // –•—É–∫–∏ –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–µ–∫ //
  const [cards, setCards] = useState([]);
  useEffect(() => {
    api.getInitialCards()
      .then(res => {
        setCards(res)
      })
      .catch((error) => console.error(error))
  }, []);

  // –•—É–∫–∏ —Å–æ—Å—Ç–æ—è–Ω–∏—è - –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–æ–≤ //
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = useState(false);
  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = useState(false);

  // –•—É–∫ –¥–ª—è –≤—ã–±–æ—Ä–∞ –∫–∞—Ä—Ç–æ—á–∫–∏ - –¥–ª—è –±–æ–ª—å—à–æ–π –∫–∞—Ä—Ç–∏–Ω–∫–∏//
  const [selectedCard, setSelectedCard] = useState(null);

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø–æ–ø–∞–ø–∞ //
  function handleCardClick(props) {
    setSelectedCard(props);
  }

  // –û—Ç–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–æ–≤ //
  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(true);
  }
  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(true);
  }
  function handleAddPlaceClick() {
    setIsAddPlacePopupOpen(true);
  }

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–æ–≤ //
  function closeAllPopups() {
    setIsEditAvatarPopupOpen(false);
    setIsEditProfilePopupOpen(false);
    setIsAddPlacePopupOpen(false);
    setSelectedCard(null);
  }

  /////////////////////////////////////////////////////////////////
  ///////////////////// –ó–∞–ø—Ä–æ—Å—ã —á–µ—Ä–µ–∑ Api /////////////////////////
  /////////////////////////////////////////////////////////////////

  // –õ–∞–π–∫–∏ –∫–∞—Ä—Ç–æ—á–∫–∏ - –ø—Ä–æ–≤–µ—Ä–∫–∞ id, –≤—ã–∑–æ–≤ api –∑–∞–ø—Ä–æ—Å–∞ //
  function handleCardLike(card) {
    const isLiked = card.likes.some(i => i._id === currentUser._id);
    api.changeLikeCardStatus(card._id, !isLiked)
      .then((newCard) => {
        const newCards = cards.map((c) => c._id === card._id ? newCard : c);
        setCards(newCards);
      })
      .catch((error) => console.error(error));
  }

   // –£–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ - –ø—Ä–æ–≤–µ—Ä–∫–∞ id, –≤—ã–∑–æ–≤ api –∑–∞–ø—Ä–æ—Å–∞ //
  function handleCardDelete(card) {
    api.deleteCard(card._id)
      .then(() => {
        const newCards = cards.filter((c) => c._id !== card._id);
        setCards(newCards);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è - –≤—ã–∑–æ–≤ api –∑–∞–ø—Ä–æ—Å–∞ //
  function handleUpdateUser(data) {
    api.editUserInfo(data)
      .then((res) => {
        set–°urrentUser(res);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  }

  // –ò–∑–º–µ–Ω–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞ - –≤—ã–∑–æ–≤ api –∑–∞–ø—Ä–æ—Å–∞ //
  function handleUpdateAvatar(image) {
    console.log(image);
    api.editUserPic(image)
      .then((image) => {
        set–°urrentUser(image);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  }

  // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ - –≤—ã–∑–æ–≤ api –∑–∞–ø—Ä–æ—Å–∞ //
  function handleAddPlaceSubmit(newCard) {
    api.addCard(newCard)
      .then((newCard) => {
        setCards([newCard, ...cards]);
        setIsAddPlacePopupOpen(false);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  ////////////////////////////////////////////////////////////

  return (
    <div className="App">
      <CurrentUserContext.Provider
        value={currentUser}>

        <Header />

        <Main
          onEditAvatar={handleEditAvatarClick}
          onEditProfile={handleEditProfileClick}
          onAddPlace={handleAddPlaceClick}
          onCardClick={handleCardClick}
          cards={cards}
          onCardLike={handleCardLike}
          onCardDelete={handleCardDelete}
        />

        <Footer />

        <EditProfilePopup
          isOpen={isEditProfilePopupOpen}
          onClose={closeAllPopups}
          onUpdateUser={handleUpdateUser}
        />

        <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose={closeAllPopups}
          onAddPlace={handleAddPlaceSubmit}
        />

        <EditAvatarPopup
          isOpen={isEditAvatarPopupOpen}
          onClose={closeAllPopups}
          onUpdateAvatar={handleUpdateAvatar}
        />

        <PopupWithForm
          name="confirm"
          title="–í—ã —É–≤–µ—Ä–µ–Ω—ã?"
          onClose={closeAllPopups}
        >
          <button
            type="submit"
            className="popup__save">
            –î–∞
          </button>
        </PopupWithForm>

        <ImagePopup
          card={selectedCard}
          onClose={closeAllPopups}
        />
      </CurrentUserContext.Provider>
    </div>
  );
}

export default App;